// Angular
import { Component, OnInit,ViewChild,ElementRef ,ChangeDetectorRef} from '@angular/core';
// Lodash
import { shuffle } from 'lodash';
// Services
import { LayoutConfigService } from '../../../core/_base/layout';
import {DashboardService}  from './dashboard.service'
// Widgets model
import { SparklineChartOptions } from '../../../core/_base/metronic';
import { Widget4Data } from '../../partials/content/widgets/widget4/widget4.component';
import { Chart } from 'chart.js';

@Component({
	selector: 'kt-dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['dashboard.component.scss'],
})

export class DashboardComponent implements OnInit {
	chartOptions1: SparklineChartOptions;
	chartOptions2: SparklineChartOptions;
	chartOptions3: SparklineChartOptions;
	chartOptions4: SparklineChartOptions;
	widget4_1: Widget4Data;
	widget4_2: Widget4Data;
	widget4_3: Widget4Data;
	widget4_4: Widget4Data;
	geoChartData:any;
	model='7';
	model1='7';
	model2='7';
	model3='7';
	model4='7';
	model5='7';
	model6='7';
	model7='7'
	//horizental bar
	chart=[];
	allDataFromReports;
	//variables of widget


	
	constructor(private layoutConfigService: LayoutConfigService,private dashboradService:DashboardService,private cahngeDetectref:ChangeDetectorRef) {
	
	this.dashboradService.getAllreports().subscribe(res=>{
		if(res)
		{
			this.allDataFromReports=res['result']
			console.log(this.allDataFromReports)
		}
	})
	}
	dateChange(event)
	{
	 this.model=`${new Date(event.value.begin).toDateString()}-${new Date(event.value.end).toDateString()}`
	 console.log(this.model)	
	}
	specifyValue(value){
     this.model=value
	}
	ngOnInit(): void {

	}
	filterActivity(range)
	{
		console.log(range)
     this.dashboradService.getActivity({'rangeTime':range}).subscribe(res=>{
		 this.allDataFromReports.Activity=res['Activity']
		 this.cahngeDetectref.detectChanges()
	 })
	}
	filterrevenu(range)
	{
		this.dashboradService.getrevenue({'rangeTime':range},null).subscribe(res=>{
			this.allDataFromReports.Revenus=res['Revenus']
			this.cahngeDetectref.detectChanges()
		})	
	}
	filterSoldmembership(range)
	{
		this.dashboradService.getSoldMembership({'rangeTime':range}).subscribe(res=>{
			this.allDataFromReports.membershipsolid=res['membershipsolid']
			this.cahngeDetectref.detectChanges()
		})	
	}
	filterSubscriber(range)
	{
		this.dashboradService.getSubscriber({'rangeTime':range}).subscribe(res=>{
			this.allDataFromReports.subscribedMembers=res['subscribedMembers']
			this.cahngeDetectref.detectChanges()
		})	
	}
	filterstaff(range)
	{
		this.dashboradService.getstaff({'rangeTime':range}).subscribe(res=>{
			this.allDataFromReports.staff=res['staff']
			this.cahngeDetectref.detectChanges()
		})	
	}

	filterDiscount(range)
	{
		this.dashboradService.getDiscount({'rangeTime':range}).subscribe(res=>{
			this.allDataFromReports.Activity=res['discount']
			this.cahngeDetectref.detectChanges()
		})	
	}	
	filterUsers(range)
	{
		this.dashboradService.getMembers({'rangeTime':range}).subscribe(res=>{
			this.allDataFromReports.users=res['discount']
			this.cahngeDetectref.detectChanges()
		})	
	}	
}
